.expense-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: o.5rem;
    margin: 1rem 0;
    background-color: #7c7c7c;
}
.expense-item_description{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: flex-end;
    flex-flow: column-reverse;
    justify-content: flex-start;
    flex: 1;
    
}

.expense-item h2{
    color: red;
    font-size: 1rem;
    flex: 1;
    margin: 0 1rem;
    color: white;
}
.expense-item_price{
    font-size: 1rem;
    font-weight: bold;
    color: white;
    background-color: #40005d;
    border: 1px solid white;
    padding: 0.5rem;
    border-radius: 12px;
    margin: 10px 10px 10px ;
}
.first{
    padding-left: 10px;
}
@media (min-width: 580px){
    .expense-item_description{
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;
        flex: 1;
        
    }
    .expense-item_description h2 {
        font-size: 1.25rem;
      }
    .expense-item__price {
        font-size: 1.25rem;
        padding: 0.5rem 1.5rem;
      }
}